// Forms Init
// ==========


/// Internet Explorer 11-
/// @group forms
button {
  overflow: visible;
}


/// border-radius: iOS 8+ |
/// line-height: Firefox 4+
/// @group forms
input {
  border-radius: 0;
  line-height: normal;
}


/// appearance, cursors, and inner-focus
/// for buttons and button-like inputs
/// (appearance: iOS 8+)
/// @group forms
button,
[type='button'],
[type='reset'],
[type='submit'] {
  appearance: button;
  cursor: pointer;

  &::-moz-focus-inner {
    border: 0 none !important;
    padding: 0 !important;
  }
}


/// Address box sizing set to `content-box` in IE 8/9/10.
/// Remove excess padding in IE 8/9/10.
/// @group forms
[type='checkbox'],
[type='radio'] {
  box-sizing: border-box;
  padding: 0;
}


/// Firefox 36+
/// @group forms
[type='number'] {
  width: auto;
}


/// Chrome 45+, Safari 9+
/// @group forms
[type='search'] {
  appearance: textfield;
}


/// Specify the background-color,
/// and the inherited color/font of form elements
/// @group forms
button,
input,
select,
textarea {
  background-color: transparent;
  color: inherit;
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit;
}


/// Remove default vertical scrollbar in IE 8/9/10/11,
/// and specify the resizability of textareas
/// @group forms
textarea {
  overflow: auto;
  resize: vertical;
}
