// Gallery Pattern
// ===============

[data-gallery] {
  margin-bottom: 1rem;
}

[data-gallery='video'] {
  height: 0;
  padding-top: percentage(9/16);
  position: relative;
}

.video-wrap {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;

  iframe {
    height: 100%;
    width: 100%;
  }
}
